import { useEffect, useState, useRef, useCallback } from "react";
import styled from "styled-components";
import {
  Button,
  Container,
  CircularProgress,
  Grid,
  Snackbar,
  useTheme,
  useMediaQuery,
  makeStyles,
} from "@material-ui/core";
import Alert from "@material-ui/lab/Alert";
import Image from "material-ui-image";

import * as anchor from "@project-serum/anchor";

import { LAMPORTS_PER_SOL } from "@solana/web3.js";

import { useAnchorWallet } from "@solana/wallet-adapter-react";
import { WalletDialogButton } from "@solana/wallet-adapter-material-ui";

import {
  CandyMachine,
  awaitTransactionSignatureConfirmation,
  getCandyMachineState,
  mintOneToken,
  shortenAddress,
} from "../lib/candyMachine";
import { PUBLIC_URL } from "../lib/constants";
import { HeaderButton } from "./HeaderButton";
import { HeaderText, Text, Title, Subtitle } from "./Typography";
import { RoadmapSection } from "./RoadmapSection";
import { CountdownTimer } from "./CountdownTimer";
import { Content } from "./HomeContent";

const ConnectButton = styled(WalletDialogButton)``;

const MintContainer = styled.div``;

const MintButton = styled(Button)``;

const useStyles = makeStyles((theme) => ({
  majorTitle: {
    [theme.breakpoints.up("xs")]: {
      margin: "50px 0",
    },
    [theme.breakpoints.up("md")]: {
      margin: "80px 0",
    },
  },
  roadmapSectionContainer: {
    [theme.breakpoints.up("xs")]: {
      padding: "50px 20px",
    },
    [theme.breakpoints.up("md")]: {
      padding: "100px 100px",
    },
  },
  roadmapSectionAlt: {
    backgroundColor: "#151515",
  },
}));

export interface HomeProps {
  candyMachineId: anchor.web3.PublicKey;
  config: anchor.web3.PublicKey;
  connection: anchor.web3.Connection;
  startDate: number | Date;
  treasury: anchor.web3.PublicKey;
  txTimeout: number;
}

const Home = (props: HomeProps) => {
  const [balance, setBalance] = useState<number>();
  const [isActive, setIsActive] = useState(false); // true when countdown completes
  const [isSoldOut, setIsSoldOut] = useState(false); // true when items remaining is zero
  const [isMinting, setIsMinting] = useState(false); // true when user got to press MINT
  const [itemsRemaining, setItemsRemaining] = useState(0);
  const aboutRef = useRef<any>(null);
  const roadmapRef = useRef<any>(null);
  const traitsRef = useRef<any>(null);
  const [lang, setLang] = useState<"en" | "esp">("esp");
  const [alertState, setAlertState] = useState<AlertState>({
    open: false,
    message: "",
    severity: undefined,
  });
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down("sm"));
  const classes = useStyles();
  const [startDate, setStartDate] = useState(new Date(props.startDate));
  const wallet = useAnchorWallet();
  const [candyMachine, setCandyMachine] = useState<CandyMachine>();

  const onMint = async () => {
    try {
      setIsMinting(true);
      if (wallet && candyMachine?.program) {
        const mintTxId = await mintOneToken(candyMachine, props.config, wallet.publicKey, props.treasury);

        const status = await awaitTransactionSignatureConfirmation(
          mintTxId,
          props.txTimeout,
          props.connection,
          "singleGossip",
          false
        );

        if (!status?.err) {
          setAlertState({
            open: true,
            message: "Congratulations! Mint succeeded!",
            severity: "success",
          });
        } else {
          setAlertState({
            open: true,
            message: "Mint failed! Please try again!",
            severity: "error",
          });
        }
      }
    } catch (error: any) {
      // TODO: blech:
      let message = error.msg || "Minting failed! Please try again!";
      if (!error.msg) {
        if (error.message.indexOf("0x138")) {
        } else if (error.message.indexOf("0x137")) {
          message = `SOLD OUT!`;
        } else if (error.message.indexOf("0x135")) {
          message = `Insufficient funds to mint. Please fund your wallet.`;
        }
      } else {
        if (error.code === 311) {
          message = `SOLD OUT!`;
          setIsSoldOut(true);
        } else if (error.code === 312) {
          message = `Minting period hasn't started yet.`;
        }
      }

      setAlertState({
        open: true,
        message,
        severity: "error",
      });
    } finally {
      if (wallet) {
        const balance = await props.connection.getBalance(wallet.publicKey);
        setBalance(balance / LAMPORTS_PER_SOL);
      }
      setIsMinting(false);
    }
  };

  useEffect(() => {
    (async () => {
      if (wallet) {
        const balance = await props.connection.getBalance(wallet.publicKey);
        setBalance(balance / LAMPORTS_PER_SOL);
      }
    })();
  }, [wallet, props.connection]);

  useEffect(() => {
    (async () => {
      if (!wallet) return;

      const { candyMachine, goLiveDate, itemsRemaining } = await getCandyMachineState(
        wallet as anchor.Wallet,
        props.candyMachineId,
        props.connection
      );

      setIsSoldOut(itemsRemaining === 0);
      setItemsRemaining(itemsRemaining);
      setStartDate(goLiveDate);
      setCandyMachine(candyMachine);
    })();
  }, [wallet, props.candyMachineId, props.connection, isMinting]);

  const toggleLang = useCallback(() => {
    if (lang === "en") {
      setLang("esp");
    } else {
      setLang("en");
    }
  }, [lang, setLang]);

  return (
    <main style={{ backgroundColor: "black" }}>
      {isMobile ? (
        <>
          <div style={{ paddingTop: 50 }}>
            <Image
              src={`${PUBLIC_URL}/banner.jpg`}
              animationDuration={1000}
              aspectRatio={1500 / 960}
              style={{ backgroundColor: "rgba(0,0,0,0)" }}
            />
            <Image
              src={`${PUBLIC_URL}/logo.png`}
              animationDuration={1000}
              style={{
                position: "absolute",
                paddingTop: 0,
                height: 100,
                width: 100,
                top: 20,
                left: 10,
                backgroundColor: "rgba(0,0,0,0)",
              }}
            />
            <div style={{ position: "absolute", top: 20, right: 10 }}>
              <HeaderButton onClick={() => toggleLang()}>{Content[lang].nav.lang}</HeaderButton>
            </div>
          </div>
          <div
            style={{
              backgroundImage: "linear-gradient(180deg, #3D3D3D 0%, #000000 100%)",
              right: 70,
              padding: 36,
              top: "50%",
              textAlign: "center",
            }}
          >
            <div style={{ fontWeight: 900, color: "white", marginBottom: 24 }}>
              <HeaderText style={{ fontSize: 32 }}>{Content[lang].hero.title}</HeaderText>
              <div>
                <CountdownTimer startDate={startDate} setIsActive={setIsActive} />
              </div>
            </div>
            <div style={{ backgroundColor: "#FFA16C", padding: "18px 20px" }}>
              <Text style={{ fontWeight: 700, fontSize: 22 }}>{Content[lang].hero.date}</Text>
            </div>
          </div>
        </>
      ) : (
        <Container>
          <div style={{ paddingTop: 98, position: "relative" }}>
            <Image
              src={`${PUBLIC_URL}/banner.jpg`}
              animationDuration={1000}
              aspectRatio={1500 / 960}
              style={{ backgroundColor: "rgba(0,0,0,0)" }}
            />
            <Image
              src={`${PUBLIC_URL}/logo.png`}
              animationDuration={1000}
              style={{
                backgroundColor: "rgba(0,0,0,0)",
                position: "absolute",
                paddingTop: 0,
                height: 200,
                width: 200,
                top: 70,
                left: 88,
              }}
            />
            <div style={{ position: "absolute", top: 70, right: 70, display: "flex", flexDirection: "row", gap: 10 }}>
              <HeaderButton
                onClick={() => {
                  if (aboutRef?.current) {
                    aboutRef.current.scrollIntoView();
                  }
                }}
              >
                {Content[lang].nav.about}
              </HeaderButton>
              <HeaderButton
                onClick={() => {
                  if (roadmapRef?.current) {
                    roadmapRef.current.scrollIntoView();
                  }
                }}
              >
                {Content[lang].nav.roadmap}
              </HeaderButton>
              <HeaderButton
                onClick={() => {
                  if (traitsRef?.current) {
                    traitsRef.current.scrollIntoView();
                  }
                }}
              >
                {Content[lang].nav.traits}
              </HeaderButton>
              <HeaderButton onClick={() => toggleLang()}>{Content[lang].nav.lang}</HeaderButton>
            </div>
            <div
              style={{
                position: "absolute",
                backgroundImage: "linear-gradient(180deg, #3D3D3D 0%, #000000 100%)",
                borderRadius: 8,
                right: 70,
                padding: 36,
                top: "50%",
                textAlign: "center",
              }}
            >
              <div style={{ fontWeight: 900, color: "white", marginBottom: 24 }}>
                <HeaderText style={{ fontSize: 32 }}>{Content[lang].hero.title}</HeaderText>
                <div>
                  <CountdownTimer startDate={startDate} setIsActive={setIsActive} />
                </div>
              </div>
              <div style={{ backgroundColor: "#FFA16C", padding: "18px 20px" }}>
                <Text style={{ fontWeight: 700, fontSize: 22 }}>{Content[lang].hero.date}</Text>
              </div>
            </div>
          </div>
        </Container>
      )}
      <Container>
        <div ref={aboutRef} style={{ padding: `${isMobile ? "20px 0 20px 0" : "70px"}`, position: "relative" }}>
          <div
            style={{
              color: "white",
              backgroundColor: "#151515",
              padding: `${isMobile ? "100px 20px 50px 20px" : "100px 50px 50px 50px"}`,
              marginTop: 100,
              borderRadius: 5,
            }}
          >
            <div className={classes.majorTitle} style={{ textAlign: "center" }}>
              <HeaderText>{Content[lang].about.header}</HeaderText>
            </div>
            <Text>{Content[lang].about.text}</Text>
            <Text>
              <br />
              <br />
            </Text>
            <Text>{Content[lang].about.text2}</Text>
          </div>
          <div
            style={{
              width: "100%",
              position: "absolute",
              top: isMobile ? 20 : 70,
              left: 0,
              display: "flex",
              justifyContent: "center",
            }}
          >
            <Image
              src={`${PUBLIC_URL}/about.png`}
              animationDuration={1000}
              style={{
                height: 200,
                width: 200,
                paddingTop: 0,
                backgroundColor: "rgba(0,0,0,0)",
              }}
            />
          </div>
        </div>

        <div ref={roadmapRef} style={{ color: "white", textAlign: "center" }}>
          <Image
            src={`${PUBLIC_URL}/roadmap.jpg`}
            animationDuration={1000}
            aspectRatio={1440 / 886}
            style={{ backgroundColor: "rgba(0,0,0,0)" }}
          />
          <div className={classes.majorTitle}>
            <HeaderText>{Content[lang].roadmapHeader}</HeaderText>
          </div>
        </div>
      </Container>
      <section className={classes.roadmapSectionAlt}>
        <Container className={classes.roadmapSectionContainer}>
          <RoadmapSection
            subtitle={Content[lang].giveaways.subtitle}
            title={Content[lang].giveaways.title}
            text={Content[lang].giveaways.text}
            image={
              <Image
                src={`${PUBLIC_URL}/giveaways.jpg`}
                animationDuration={1000}
                aspectRatio={575 / 360}
                style={{ backgroundColor: "rgba(0,0,0,0)" }}
              />
            }
          />
        </Container>
      </section>
      <section>
        <Container className={classes.roadmapSectionContainer}>
          <RoadmapSection
            reverse={!isMobile}
            subtitle={Content[lang].cryptopens.subtitle}
            title={Content[lang].cryptopens.title}
            text={Content[lang].cryptopens.text}
            image={
              <Image
                src={`${PUBLIC_URL}/cryptopens.png`}
                animationDuration={1000}
                style={{ backgroundColor: "rgba(0,0,0,0)" }}
              />
            }
          />
        </Container>
      </section>
      {/* <section className={classes.roadmapSectionAlt}>
        <Container>
          <RoadmapSection
            subtitle="October 31st"
            title="Secondary Sales on Magic Eden"
            text="Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Suspendisse sed nisi lacus sed. In hac habitasse platea dictumst vestibulum rhoncus est. Lacus viverra vitae congue eu consequat ac."
            image={
              <Image
                src={`${PUBLIC_URL}/magiceden.png`}
                animationDuration={1000}
                aspectRatio={415 / 73}
                style={{ backgroundColor: "rgba(0,0,0,0)" }}
              />
            }
          />
        </Container>
      </section> */}
      <section className={classes.roadmapSectionAlt}>
        <Container className={classes.roadmapSectionContainer}>
          <RoadmapSection
            subtitle={Content[lang].allsolday.subtitle}
            title={Content[lang].allsolday.title}
            text={Content[lang].allsolday.text}
            image={
              <Image
                src={`${PUBLIC_URL}/allsolday.png`}
                animationDuration={1000}
                style={{ backgroundColor: "rgba(0,0,0,0)" }}
              />
            }
          />
        </Container>
      </section>
      <section>
        <Container className={classes.roadmapSectionContainer}>
          <RoadmapSection
            reverse={!isMobile}
            subtitle={Content[lang].merch.subtitle}
            title={Content[lang].merch.title}
            text={Content[lang].merch.text}
            image={
              <Image
                src={`${PUBLIC_URL}/heavenlymerch.png`}
                animationDuration={1000}
                style={{ backgroundColor: "rgba(0,0,0,0)" }}
              />
            }
          />
        </Container>
      </section>
      <section>
        <Container style={{ textAlign: "center", paddingBottom: 50 }}>
          <div ref={traitsRef} className={classes.majorTitle} style={{ color: "white" }}>
            <HeaderText>{Content[lang].traitsHeader}</HeaderText>
          </div>
          <div
            style={{
              backgroundColor: "#151515",
              borderRadius: 5,
              margin: "50px 50px 0 50px",
              padding: "200px 0",
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
            }}
          >
            <Title style={{ color: "rgba(255, 255, 255, 0.48)", fontWeight: 400, margin: "0 50px" }}>
              {Content[lang].comingSoon}
            </Title>
          </div>
        </Container>
      </section>

      {/* <Grid
        container
        justifyContent="center"
        alignItems="center"
        direction="column"
        justify="center"
        style={{ minHeight: "30vh" }}
      >
        <Grid item xs={3}>
          {wallet && (
            <Grid item>
              <p>Address: {shortenAddress(wallet.publicKey.toBase58() || "")}</p>
            </Grid>
          )}

          {wallet && <p>My Balance: {(balance || 0).toLocaleString()} SOL</p>}

          {wallet && <p>NFTs remaining: {itemsRemaining}</p>}

          <MintContainer>
            {!wallet ? (
              <ConnectButton>Connect Wallet</ConnectButton>
            ) : (
              <MintButton disabled={isSoldOut || isMinting || !isActive} onClick={onMint} variant="contained">
                {isSoldOut ? (
                  "SOLD OUT"
                ) : isActive ? (
                  isMinting ? (
                    <CircularProgress />
                  ) : (
                    "MINT"
                  )
                ) : (
                  <Countdown
                    date={startDate}
                    onMount={({ completed }) => completed && setIsActive(true)}
                    onComplete={() => setIsActive(true)}
                    renderer={renderCounter}
                  />
                )}
              </MintButton>
            )}
          </MintContainer>
        </Grid>
      </Grid> */}
      <Snackbar
        open={alertState.open}
        autoHideDuration={10000}
        onClose={() => setAlertState({ ...alertState, open: false })}
      >
        <Alert onClose={() => setAlertState({ ...alertState, open: false })} severity={alertState.severity}>
          {alertState.message}
        </Alert>
      </Snackbar>
    </main>
  );
};

interface AlertState {
  open: boolean;
  message: string;
  severity: "success" | "info" | "warning" | "error" | undefined;
}

export default Home;
