import React from "react";
import { Subtitle, Text, Title } from "./Typography";
import Grid from "@material-ui/core/Grid";

export const RoadmapSection = ({ title, subtitle, text, image, reverse = false }) => {
  const content = [
    <Grid item md={7} xs={12} key="content">
      <div>
        <Subtitle>{subtitle}</Subtitle>
      </div>
      <div style={{ margin: "30px 0" }}>
        <Title>{title}</Title>
      </div>
      <div>
        <Text>{text}</Text>
      </div>
    </Grid>,
    <Grid item md={5} xs={12} key="image">
      {image}
    </Grid>,
  ];

  return (
    <Grid container spacing={6} style={{ color: "white", alignItems: "center" }}>
      {reverse ? content.reverse() : content}
    </Grid>
  );
};
